<template>
  <div id="app">
    <div v-if="openWindowNo==-1" class="buttons-holder">
      <div class="button-holder">
        <div v-if="!selectedDocument" class="button-label">Toggle Annotations</div>
        <div class="circular-button" @click=toggleInterlinear>
          <i v-if="hasInterlinear" class="icon material-icons small-icon not-selectable">speaker_notes_off</i>
          <i v-else class="icon material-icons not-selectable">speaker_notes</i>
        </div>
      </div>
      <div class="button-holder">
        <div v-if="!selectedDocument" class="button-label">Lemmatize Text</div>
        <div class="circular-button" @click=openWindow(0)><i class="icon material-icons small-icon not-selectable">translate</i></div>
      </div>
      <div class="button-holder">
        <div v-if="!selectedDocument" class="button-label">About</div>
        <div class="circular-button" @click=openWindow(1)><i class="icon material-icons small-icon not-selectable">help_outline</i></div>
      </div>
      <div v-if="collection && collection.documents.length > 1" class="button-holder">
        <div v-if="!selectedDocument" class="button-label">Document Sections</div>
        <div class="circular-button" @click=openWindow(2)><i class="icon material-icons small-icon not-selectable">format_list_numbered</i></div>
      </div>
      <div class="button-holder">
        <div v-if="!selectedDocument" class="button-label">Clear Document</div>
        <div class="circular-button" @click=reset><i class="icon material-icons small-icon not-selectable">replay</i></div>
      </div>
    </div>
    <div v-if="selectedDocument">
      <Document v-bind:document="selectedDocument" v-bind:hasInterlinear="hasInterlinear"/>
    </div>
    <div v-else class="home-page">
      <div class="main-title">UYGHUR</div>
      <div class="secondary-title">LEMMATIZER</div>
    </div>
    <div v-if="openWindowNo>=0" class="window">
      <div class="window-content">
        <template v-if="openWindowNo==0">
          <div class="window-header">Convert Content</div>
          <div class="window-body">
            <div>Copy Uyghur-language text or URL for conversion.</div>
            <div class="warning-box">
              <div class="warning-text">WARNING</div>
              <div class="disclaimer-text">By clicking 'paste' you agree to send your clipboard contents for conversion in the cloud. Information received by the app is anonymized and will only be used to improve the app's performance.</div>
              <div class="input-holder">
                <div class="input-text">
                  <div>{{inputText}}</div>
                </div>
                <div class="go-button" @click=paste>Paste</div>
              </div>
            </div>
          </div>
        </template>
        <template v-if="openWindowNo==1">
          <div class="window-header">About</div>
          <div class="window-body"></div>
        </template>
        <template v-if="openWindowNo==2">
          <div class="window-header">Sections</div>
          <div class="window-body">
            <div v-if="collection" class="document-buttons">
              <span v-for="(document, iDoc) in collection.documents"
                  :key=iDoc v-on:click=selectDocument(iDoc) class="document-button">
                  {{iDoc}}
              </span>
            </div>
          </div>
        </template>
        <div class="window-footer">
          <div class="interlinear-button" @click=closeWindow><i class="icon material-icons big-icon not-selectable">highlight_off</i></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Document from './components/Document.vue';
export default {
  name: 'App',
  components: {
    Document
  },
  data:function(){
    return {
      collection:null,
      entries:[],
      position:{x:0, y:0},
      selectedDocument:null,
      popupIsVisible:false,
      inputText:'',
      hasInterlinear:false,
      openWindowNo:-1
    }
  },
  methods:{
    reset(){
      console.log('reset');
      this.collection = null;
      this.inputText = '';
      this.selectedDocument = null;
    },
    openWindow(windowNo){
      this.openWindowNo=windowNo;
    },
    closeWindow(){
      this.openWindowNo=-1;
    },
    toggleInterlinear(){
      console.log('toggleinterlinear')
      this.hasInterlinear = !this.hasInterlinear;
    },
    getType(text){
      let tokens = text.split(" ");
      let type = 'text';
      if(tokens.length < 4){
        tokens.forEach(token => {
          if(token.match(/\w\.\w/)) type = 'url';
        });
      }
      return type;
    },
    paste(){
      navigator.clipboard.readText().then(text => {
        this.inputText = text;
        this.updateCollection(text, this.getType(text));
      });
    },
    updateCollection(text, type){
      if(!type) type = 'text';
      const url = 'https://us-central1-warm-mediator-289819.cloudfunctions.net/test-func';
      const request = {
        method:'POST',
        headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body:JSON.stringify({
          'text':text,
          'type':type
        })
      };
      let that = this;
      fetch(url, request)
        .then(response => {
          response.json().then(data => {
            that.collection = data.collection;
            that.selectDocument(0);
            console.log(that.collection);
          });
        });
    },
    selectDocument(index){
      console.log('select');
      this.selectedDocument = this.collection.documents[index];
    }
  },
  mounted(){}
}
</script>

